//@import "neat/neat"; // or "neat" when in Rails
@import "../libraries/neat/app/assets/stylesheets/neat";

body {
	color: $base-font-color;
	//font-family: $base-font-family;
	font-family: et-book;
	font-size: $base-font-size;
	line-height: $base-line-height;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $heading-font-family;
	font-size: modular-scale(1);
	line-height: $heading-line-height;
	margin: 0 0 $small-spacing;
	font-family: et-book;
}

p {
	margin: 0 0 $small-spacing;
}

a {
	color: $action-color;
	text-decoration: none;
	transition: color $base-duration $base-timing;

	&:active,
	&:focus,
	&:hover {
		color: shade($action-color, 25%);
	}
}

hr {
	border-bottom: $base-border;
	border-left: 0;
	border-right: 0;
	border-top: 0;
	margin: $base-spacing 0;
}

/*
 *copied from refills
 */
$serif: $font-stack-georgia;
$sans-serif: $font-stack-helvetica;
h1, h2, h3, p {
	margin: 0;
}

hr {
	border-bottom: 1px solid $light-gray;
	border-left: 0;
	border-right: 0;
	border-top: 0;
	margin: $base-spacing 0;
}

p {
	color: $base-font-color;
	line-height: $base-line-height;
}

a {
	color: $action-color;
	text-decoration: none;
}

.type {
	border-bottom: 2px solid;
	display: inline-block;
	font-family: $sans-serif;
	font-size: 0.7em;
	font-weight: 800;
	margin-bottom: 2em;
	padding: 0.1em 0;
	text-align: left;
	text-transform: uppercase;
}

h1 {
	font-family: $serif;
	font-size: 1.9em;
	//font-weight: 700;
	margin-bottom: 0.3em;

	@include media($medium-screen) {
		font-size: 2.6em;
	}
}
h2 {
	font-family: $serif;
	font-size: 1.3em;
	font-weight: 400;
	line-height: 1.25em;
	margin-bottom: 0.9em;

	@include media($medium-screen) {
		font-size: 1.5em;
	}
}

code {
	background: #F7F7F7;
	border-radius: $base-border-radius * 1.5;
	border: 1px solid #E0E0E0;
	font-size: 0.75em;
	font-style: normal;
	padding: 0.1em 0.4em;
	white-space: nowrap;
}

h2 code {
	font-size: 0.65em;
}

h3 {
	font-family: $serif;
	font-size: 1.4em;
	font-style: italic;
	font-weight: 400;
	line-height: 1.3em;
	margin-bottom: 0.4em;
}

p.date {
	color: transparentize($base-font-color, 0.6);
	font-family: $serif;
	font-style: italic;
	margin-bottom: 0.3em;
}

p {
	font-family: $sans-serif;
	letter-spacing: 1;
	line-height: 1.55em;
	margin-bottom: 1.5em;

	span {
		font-family: $serif;
		font-size: 1.2em;
		font-style: italic;
	}
}

a.read-more {
	display: inline-block;
	font-family: $sans-serif;
	font-size: 0.8em;
	font-weight: 700;
	margin-left: 0.2em;
	position: relative;
	text-transform: uppercase;

	span {
		font-family: $sans-serif;
		font-size: 1.5em;
		font-style: normal;
		position: absolute;
		right: -12px;
		top: -1px;
	}
}

hr {
	width: 3em;
}

p.author {
	font-family: $serif;
	font-size: 1.2em;
	font-style: italic;
}
